<template>
  <div class="case">
    <NavbarMenu />
    病例学习

    <v-app class="login">
      <div class="login-background">
        <div class="header">
          <span>病种选择</span>
        </div>
        <div class="topicmain">
          <div class="panels">
            <v-row justify="center">
              <v-expansion-panels accordion>
                <v-expansion-panel v-for="(item, key1) in items" :key="key1">
                  <v-expansion-panel-header>{{
                    item.categoryName
                  }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-btn
                      color="primary"
                      elevation="2"
                      v-for="(aitem, key2) in item.diseases"
                      :key="key2"
                      @click="to(aitem.diseaseId)"
                      >{{ aitem.diseaseName }}</v-btn
                    >
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
            </v-row>
          </div>
        </div>
      </div>
    </v-app>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "Casemanage",
  components: {
    NavbarMenu: () => import("../components/NavbarMenu"),
  },
  data() {
    return {
      items: [
        // { 
        //   categoryName: "传染病" ,
        //   // categoryId:'010',
        //   diseases:[
        //     {diseaseId:'020',diseaseName:'疾病1'},
        //     {diseaseId:'021',diseaseName:'疾病2'},
        //     {diseaseId:'022',diseaseName:'疾病3'},
        //   ]
        // },
        // { 
        //   categoryName: "寄生虫病" ,
        //   // categoryId:'011',
        //   diseases:[
        //     {diseaseId:'020',diseaseName:'疾病1'},
        //     {diseaseId:'021',diseaseName:'疾病2'},
        //     {diseaseId:'022',diseaseName:'疾病3'},
        //   ]
        // },
        // { 
        //   categoryName: "内科" ,
        //   // categoryId:'012',
        //   diseases:[
        //     {diseaseId:'020',diseaseName:'疾病1'},
        //     {diseaseId:'021',diseaseName:'疾病2'},
        //     {diseaseId:'022',diseaseName:'疾病3'},
        //   ]
        // },
      ],
    };
  },
  created(){
    this.updata()
  },
  methods: {
    updata(){
      axios.get('/listall').then((res)=>{
        console.log('res:',res)
        if (res.data.code === 200){
          this.items = res.data.data
        }else if (res.data.code === -1) {
          window.sessionStorage.clear(); // 清空token
          var href = res.data.data.domain;
          window.location.href = href;
        }
        else {
          this.$store.dispatch('snackbar/openSnackbar',{
            msg:res.data.msg,
            color:'primary'
          })
        }
      })
    },
    to(diseaseId) {
      this.$router.push({
        path:'/case/real',
        query: {
          caseid:diseaseId,      // 大类下具体病种
        }
      })
    },
  },
};
</script>


